<template>
  <div>
    <form @submit.prevent="upload" enctype="multipart/form-data">
      <v-file-input multiple v-model="image" @change="imagePick"></v-file-input>
      <v-container fluid v-for="(image, index) in imagePath" :key="index">
        <v-layout row wrap>
          <v-flex md4 xs6>
            <img :src="image" width="400px" />
          </v-flex>
        </v-layout>
      </v-container>
    </form>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  data() {
    return {
      image: null,
      imagePath: [],
    };
  },
  methods: {
    async imagePick() {
      let images = this.image;
      let arrImage = this.imagePath;
      if (images.length > 0) {
        images.forEach((value) => {
          const fileReader = new FileReader();
          fileReader.readAsDataURL(value);
          fileReader.addEventListener("load", function() {
            arrImage.push(fileReader.result);
          });
        });
      } else {
        this.imagePath = [];
      }
    },
  },
  upload() {
    let images = this.image;
    let formData = new FormData();
    for (let value of images) {
      formData.append("image", value);
    }

    // for (var [key, value] of formData.entries()) {
    //   console.log(key, value);
    // }
    // axios.post("http://localhost:3000/test", formData).then((res) => {
    //   console.log(res);
    // });
  },
};
</script>

